.parent {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: repeat(6, minmax(auto, auto));
  grid-column-gap: 20px;
  grid-row-gap: 15px;
  position: relative;
  margin: auto;
  margin-top: 10px;
  overflow-y: scroll;
  width: 70vw;
  background: #F5F5F5;
  box-shadow: 5px 5px 15px rgba(rgb(53, 52, 52), .5);
  border-radius: 10px;
  color: rgb(160, 179, 195);
  padding: 20px;
  }
  
  .parent .div1 { grid-area: 1 / 1 / 2 / 2; }
  .parent .div2 { grid-area: 2 / 1 / 5 / 2; }
  .parent .div3 { grid-area: 5 / 1 / 6 / 2; }
  .parent .div4 { grid-area: 6 / 1 / 7 / 2; }
  .parent .div5 { grid-area: 1 / 2 / 2 / 3; }
  .parent .div6 { grid-area: 2 / 2 / 5 / 3; }
  .parent .div7 { grid-area: 5 / 2 / 6 / 3; }
  .parent .div8 { grid-area: 6 / 2 / 7 / 3; }
  .parent .div9 { grid-area: 1 / 3 / 2 / 4; }
  .parent .div10 { grid-area: 2 / 3 / 3 / 4; }
  .parent .div11 { grid-area: 3 / 3 / 4 / 4; }
  .parent .div12 { grid-area: 4 / 3 / 5 / 4; }
  .parent .div13 { grid-area: 5 / 3 / 6 / 4; }
  .parent .div14 { grid-area: 6 / 3 / 7 / 4; }

  .parent label {
    font-size: 1.6rem;
    font-weight: bold;
  }

  .parent textarea {
    height: 90%;
    width: 90%;
    font-size: 1.4rem;
    margin-top: 10px;
    padding: 10px;
    overflow-y: scroll;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }

  .parent input {
    height: 90%;
    width: 90%;
    font-size: 1.4rem;
    margin-top: 10px;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }

  .parent select {
    max-height: 60px;
    margin-top: 10px;
    width: 90%;
    font-size: 1.4rem;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }


