.alertSuccess {
  position: absolute;
  z-index: 3;
  background-color: rgb(60, 60, 82);
  border: 15px solid white;
  box-shadow: 0 0 10px black;
  padding: 60px;
  font-size: 2.4rem;
  bottom: 60%;
  left: 30%;
  -webkit-animation: 5.5s fadeInUpBig forwards;
          animation: 5.5s fadeInUpBig forwards;

  @-webkit-keyframes fadeInUpBig {
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, 2000px, 0);
      transform: translate3d(0, 2000px, 0);
    }
  
    10% {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    70% {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  
    100% {
      opacity: 0;
      -webkit-transform: translate3d(0, 2000px, 0);
      transform: translate3d(0, 2000px, 0);
    }
  }

  @keyframes fadeInUpBig {
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, 2000px, 0);
      transform: translate3d(0, 2000px, 0);
    }
  
    10% {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    70% {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  
    100% {
      opacity: 0;
      -webkit-transform: translate3d(0, 2000px, 0);
      transform: translate3d(0, 2000px, 0);
    }
  }

  
}



.shop-item {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: #F5F5F5;
  height: 300px;
  box-shadow: 5px 5px 15px rgba(53, 52, 52, .5);
  border-radius: 10px;
  color: rgb(160, 179, 195);
  padding: 10px;
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
  margin: 15px;
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}

.shop-item:hover img {
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
  -webkit-filter: brightness(10%);
          filter: brightness(10%);
}

.shop-item img {
  max-width: 100%;
  max-height: 100%;
}

